import React, { useState, useEffect } from "react";
import DataTable from "react-data-table-component";
import TopBar from "../../../components/Users/UserDashboard/TopBar.jsx";
import ModuleSideNav from "../../../components/Users/UserDashboard/ModuleSideNav.jsx";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  SectionWrapper,
  Wrapper,
} from "../../../components/Styles/MainHeadStyles.js";
import { DataRoomSection } from "../../../components/Styles/DataRoomStyle.js";
import axios from "axios";
import { FaEye, FaTrash, FaEdit } from "react-icons/fa"; // FontAwesome icons
import { Link, useNavigate } from "react-router-dom";
import DangerAlertPopup from "../../../components/Admin/DangerAlertPopup";
export default function UserSignatoryList() {
  const navigate = useNavigate();
  const storedUsername = localStorage.getItem("OwnerLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [records, setrecords] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [userRole, setUserRole] = useState("Admin"); // Example role
  const [showPopup, setShowPopup] = useState(false);
  const [messagesuccessError, setmessagesuccessError] = useState("");
  const [errr, seterrr] = useState(false);
  const [DeleteId, setDeleteId] = useState("");
  const [dangerMessage, setdangerMessage] = useState("");
  const apiUrlCompany = "https://blueprintcatalyst.com/api/user/company/";
  document.title = "All Signatories";

  useEffect(() => {
    getUserSignatory();
  }, []);

  const getUserSignatory = async () => {
    const formData = {
      user_id: userLogin.id,
    };
    try {
      const resp = await axios.post(
        apiUrlCompany + "getUserSignatory",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      setrecords(resp.data.results);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const [selectedRows, setSelectedRows] = useState([]);

  const columns = [
    {
      name: "Name",
      selector: (row) => row.first_name + " " + row.last_name,
      sortable: true,
    },

    {
      name: "Company Name",
      selector: (row) => row.company_name,
      sortable: true,
    },
    {
      name: "Email",
      selector: (row) => row.signatory_email,
      sortable: true,
    },
    {
      name: "Role",
      cell: (row) => (
        <div title={row.signature_role || ""}>{row.signature_role}</div>
      ),
      sortable: true,
    },
    {
      name: "Status",
      cell: (row) => {
        const statusStyles = {
          active: {
            backgroundColor: "#d4edda",
            color: "#155724",
          },
          pending: {
            backgroundColor: "#fff3cd",
            color: "#856404",
          },
        };

        const style = statusStyles[row.access_status] || {
          backgroundColor: "#f8f9fa",
          color: "#212529",
        };

        // Capitalize first letter
        const capitalize = (text) => {
          if (!text) return "";
          return text.charAt(0).toUpperCase() + text.slice(1).toLowerCase();
        };

        return (
          <div
            title={row.access_status || ""}
            style={{
              ...style,
              padding: "5px 10px",
              borderRadius: "5px",
              textAlign: "center",
              minWidth: "80px",
            }}
          >
            {capitalize(row.access_status)}
          </div>
        );
      },
      sortable: true,
    },
    {
      name: "Action",
      cell: (row) => (
        <div className="d-flex gap-2">
          <button
            className="icon_btn green_clr"
            type="button"
            onClick={() => handleViewrole(row.signature_role)}
            title="View Role"
          >
            <FaEye /> View Role
          </button>
          <button
            className="icon_edit icon_btn"
            type="button"
            onClick={() => handleDelete(row.id)}
            title="Edit Role"
          >
            <FaEdit /> Edit Role
          </button>
          <button
            className="icon_btn red_clr"
            type="button"
            onClick={() => handleDelete(row.id)}
            title="Delete"
          >
            <FaTrash /> Delete
          </button>
        </div>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
      width: "320px",
    },
  ];
  const handleViewrole = (viewrole) => {
    setIsModalOpen(true);
    setUserRole(viewrole);
  };
  const handleDelete = (idd) => {
    setDeleteId(idd);
    setdangerMessage("Are you sure? You want to delete this");
  };
  const handleConfirm = async () => {
    setdangerMessage("");
    const formData = {
      user_id: userLogin.id,
      id: DeleteId,
    };
    try {
      const resp = await axios.post(
        apiUrlCompany + "userDeleteSignatory",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      setmessagesuccessError("Signatory deleted successfully");
      getUserSignatory();
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };

  const customStyles = {
    table: {
      style: {
        border: "1px solid #dee2e6",
        borderRadius: "12px",
        overflow: "auto",
      },
    },
    headCells: {
      style: {
        backgroundColor: "#efefef",
        fontWeight: "600",
        fontSize: "0.8rem",
        color: "#000",
        textTransform: "uppercase",
        whiteSpace: "nowrap",
      },
    },
    cells: {
      style: {
        whiteSpace: "nowrap",
        overflow: "hidden",
        textOverflow: "ellipsis",
      },
    },
    rows: {
      style: {
        fontSize: "0.8rem",
        fontWeight: "500",
      },
      stripedStyle: {
        backgroundColor: "#fff",
      },
    },
    pagination: {
      style: {
        marginTop: "15px",
        backgroundColor: "#fafafa",
        padding: "12px 16px",
      },
    },
  };

  const conditionalRowStyles = [
    {
      when: (row) => true, // apply to all rows
      style: {
        "&:hover": {
          backgroundColor: "var(--lightRed)", // apna hover color
        },
      },
    },
  ];

  const [searchText, setSearchText] = useState("");

  // Filter data by nameofreport (case insensitive)
  const filteredData = records.filter((item) => {
    const fullName = `${item.first_name || ""} ${
      item.last_name || ""
    }`.toLowerCase();
    const companyName = (item.company_name || "").toLowerCase();
    const email = (item.signatory_email || "").toLowerCase();
    const role = (item.signature_role || "").toLowerCase();
    const access_status = (item.access_status || "").toLowerCase();

    const search = searchText.toLowerCase();

    return (
      fullName.includes(search) ||
      companyName.includes(search) ||
      email.includes(search) ||
      role.includes(search) ||
      access_status.includes(search)
    );
  });

  //Share Report

  const [isCollapsed, setIsCollapsed] = useState(false);
  return (
    <>
      <>
        <Wrapper>
          <div className="fullpage d-block">
            <div className="d-flex align-items-start gap-0">
              <ModuleSideNav
                isCollapsed={isCollapsed}
                setIsCollapsed={setIsCollapsed}
              />
              <div
                className={`global_view ${
                  isCollapsed ? "global_view_col" : ""
                }`}
              >
                <TopBar />
                <SectionWrapper className="d-block p-md-4 p-3">
                  <div className="container-fluid">
                    {messagesuccessError && (
                      <div
                        className={`flex items-center justify-between gap-3 shadow-lg ${
                          errr ? "error_pop" : "success_pop"
                        }`}
                      >
                        <div className="d-flex align-items-center gap-2">
                          <span className="d-block">{messagesuccessError}</span>
                        </div>

                        <button
                          type="button"
                          className="close_btnCros"
                          onClick={() => setmessagesuccessError("")}
                        >
                          ×
                        </button>
                      </div>
                    )}
                    {dangerMessage && (
                      <DangerAlertPopup
                        message={dangerMessage}
                        onConfirm={handleConfirm}
                        onCancel={() => {
                          setdangerMessage("");
                        }}
                      />
                    )}
                    <DataRoomSection className="d-flex flex-column gap-2">
                      <div className="titleroom flex-wrap gap-3 d-flex justify-content-between align-items-center border-bottom pb-3">
                        {/* Heading on the left */}
                        <div className="pb-3 bar_design">
                          <h4 className="h5 mb-0">All Signatories</h4>
                        </div>
                        {/* Buttons on the right */}
                        <div className="d-flex gap-2">
                          {/* <Button
                            onClick={handleshareReport}
                            type="button"
                            className="btn btn-outline-dark d-flex align-items-center active gap-2"
                            style={{
                              opacity: selectedRows.length === 0 ? 0.5 : 1,
                              pointerEvents:
                                selectedRows.length === 0 ? "none" : "auto",
                            }}
                          >
                            <FaShareAlt style={{ fontSize: "14px" }} />
                            Share Report
                          </Button> */}

                          <Link
                            to="/user/addcompany"
                            className="btn btn-outline-dark active"
                          >
                            Add New Signatory
                          </Link>
                        </div>
                      </div>

                      <div className="d-flex justify-content-end my-2 p-0">
                        <input
                          type="search"
                          placeholder="Search Here..."
                          className="form-control"
                          value={searchText}
                          onChange={(e) => setSearchText(e.target.value)}
                          style={{
                            padding: "10px 15px",
                            width: "100%",
                            maxWidth: "300px",
                            fontSize: "14px",
                            borderRadius: "10px",
                          }}
                        />
                      </div>
                      <div className="d-flex flex-column overflow-auto justify-content-between align-items-start tb-box">
                        <DataTable
                          customStyles={customStyles}
                          conditionalRowStyles={conditionalRowStyles}
                          columns={columns}
                          className="datatb-report"
                          data={filteredData}
                          pagination
                          highlightOnHover
                          striped
                          responsive
                        />
                      </div>
                    </DataRoomSection>
                  </div>
                </SectionWrapper>
              </div>
            </div>
          </div>
        </Wrapper>
        {isModalOpen && (
          <div
            style={{
              position: "fixed",
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: "rgba(0,0,0,0.5)",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              zIndex: 1000,
            }}
          >
            <div
              style={{
                backgroundColor: "#fff",
                padding: "20px",
                borderRadius: "8px",
                width: "90%",
                maxWidth: "400px",
                textAlign: "left",
                boxShadow: "0 2px 8px rgba(0, 0, 0, 0.3)",
                wordWrap: "break-word",
              }}
            >
              <h3 style={{ marginTop: 0 }}>Role</h3>
              <p style={{ fontSize: "14px", color: "#333" }}>{userRole}</p>
              <div style={{ textAlign: "right" }}>
                <button
                  style={{
                    padding: "10px 20px",
                    marginTop: "15px",
                    borderRadius: "5px",
                    border: "none",
                    backgroundColor: "#212529",
                    color: "#fff",
                    cursor: "pointer",
                  }}
                  onClick={() => setIsModalOpen(false)}
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        )}
      </>
    </>
  );
}
